.logo{
    width: 30%;
    margin-top: 40px;
    z-index: 1;
}
.img-liane{
    background-image: url('../../assets/images/liane_g.png');
    background-repeat: no-repeat;
    background-size: contain;
    width: 42vw;
    height: 40vh;
    position: absolute;
    left: -2.5vw;
    top: 5vh;
    z-index: -1;
    &--d{
        background-image: url('../../assets/images/liane_d.png');
        background-position: right;
        right: 0;
        left: inherit;
        width: 40vw;
        height: 60vh;
    }
}
