body {
    font-family: 'Baloo 2', cursive;
    font-size: 1.4rem;
    background-color: $c-main;
    margin: 0;
}
.container{
    width: 80%;
    margin: auto;
}
section{
    padding-top: 60px;
    padding-bottom: 60px;
}
.no-margin{
    margin: 0;
}
.clear{
    clear: both;
}
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
.hidden{
    visibility: hidden;
    display: none;
}
