.btn{
    background-color: $c-btn;
    padding-left: 20px;
    padding-right: 20px;
    text-decoration: none;
    color: $p-btn;
    text-shadow: 0px .75px $c-btn-dark;
    box-shadow: -0px 4px 0px rgba(0, 0, 0, .5);
    transition: all .25s ease-out;
    &--big{
        font-size: 2rem;
        display: block;
        margin: auto;
        width: 25%;
        text-align: center;
    }
    &--header{
        margin-top: 40vh;
    }
    &:hover{
        // margin-top: 8px;
        background-color: $c-btn-dark;
        box-shadow: -0px 2px 0px rgba(0, 0, 0, .5);
        transition: all .25s ease-out;
    }
}
